@import url(https://fonts.googleapis.com/css2?family=Kanit&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kanit&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: "Kanit", sans-serif;
  background: rgb(21, 193, 255);
  background: linear-gradient(
    0deg,
    rgba(21, 193, 255, 1) 0%,
    rgba(44, 218, 254, 1) 6%,
    rgba(100, 226, 252, 1) 12%,
    rgba(172, 240, 254, 1) 29%,
    rgba(220, 249, 255, 1) 44%,
    rgba(255, 255, 255, 1) 63%
  );
}

body {
  min-width: 480px;
}
.gridHeader {
  display: grid;
  grid-template-rows: 1fr 1fr 1.7fr;
}

.mapIconHead {
  position: absolute;
  margin-top: 5px;
  margin-left: 15px;
  z-index: 10;
}

.provinceSelectorHeader {
  font-family: "Kanit", sans-serif;
  width: 170px;
  height: 34px;
  border: 1.8px solid rgb(207, 206, 206);
  background: rgb(247, 247, 247);
  border-right: none;
  padding-left: 30px;
  border-radius: 8px 0 0 8px;
  color: rgb(70, 70, 70);
}

.searchBar {
  height: 4vh;
  background-color: white;
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 10px;
}

.header {
  height: 5vh;
  background-color: rgb(30, 60, 120);
  color: white;
  padding: 5px;
  display: flex;
  justify-content: left;
  align-items: center;
}

.homeAndSearch {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  grid-column-gap: 10px;
  -webkit-column-gap: 10px;
          column-gap: 10px;
  margin-left: 5vw;
}

.hardCodedText1 {
  color: white;
  text-decoration: underline;
}
.hardCodedText2 {
  margin-left: 1.5vw;
}

.logoImg {
  content: url(/ywc-website/static/media/klklogodesktop.9e722e33.png);
  width: 120px;
  height: 45px;
  margin-right: 1vw;
}

.search {
  width: 60%;
  position: relative;
  display: flex;
}

.searchTerm {
  width: 100%;
  border: 1.8px solid rgb(207, 206, 206);
  border-right: none;
  padding: 5px;
  height: 20px;
  border-radius: 0x 0 0 8px;
  outline: none;
  color: #dfdfdf;
  font-family: "Kanit", sans-serif;
}

.searchTerm:focus {
  color: rgb(71, 71, 71);
}

.searchButton {
  width: 70px;
  height: 34px;
  border: 1.8px solid rgb(207, 206, 206);
  border-left: none;
  background: rgb(236, 235, 235);
  text-align: center;
  color: #fff;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  font-size: 13px;
}

@media (max-width: 480px) {
  .mapIconHead {
    display: none;
  }

  .provinceSelectorHeader {
    display: none;
  }

  .hardCodedText2 {
    margin-left: 5vw;
    font-size: 2vh;
  }

  .logoImg {
    content: url(/ywc-website/static/media/klklogomobile.8c3bf438.png);
    width: 90px;
    height: 55px;
  }

  .search {
    width: 70%;
    position: relative;
    display: flex;
  }

  .searchBar {
    min-width: 480px;
    height: 1vh;
    background-color: white;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    padding: 30px 10px 10px 10px;
    margin-bottom: 10px;
  }

  .filterButton {
    margin-left: 10px;
    background-color: Transparent;
    border: none;
  }

  .filterIcon {
    width: 23px;
    height: 23px;
  }
}

@media screen and (min-width: 481px) {
  .filterButton {
    display: none;
  }
}

body {
  font-family: "Kanit", sans-serif;
}

.grid {
  display: grid;
  min-width: 480px;
  grid-template-columns: 1fr 3fr;
  grid-template-areas: "leftBody rightBody";
  grid-column-gap: 4vw;
  -webkit-column-gap: 4vw;
          column-gap: 4vw;
  padding: 1vw;
}

.leftBody {
  grid-area: leftBody;
  border: 1px solid rgb(200, 200, 200);
  background-color: rgb(255, 255, 255);
  width: 100%;
  height: auto;
  padding: 1vw;
}

.textHeadLeft {
  font-size: 2vmin;
  font-weight: 1000;
  padding: 10px;
}

.textHeadLeft2 {
  font-size: 2vmin;
  font-weight: 1000;
  padding: 7px;
  margin-top: 20px;
}

.provinceBody {
  position: absolute;
  z-index: 10;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  grid-column-gap: 10px;
  -webkit-column-gap: 10px;
          column-gap: 10px;
  margin-top: 40px;
  margin-left: 10px;
}

.provinceSelector {
  font-family: "Kanit", sans-serif;
  width: 100%;
  height: 4vh;
  border: 1px solid rgb(220, 220, 220);
  padding-left: 30px;
  background: rgb(247, 247, 247);
  color: rgb(70, 70, 70);
}
.subcateSelector {
  font-family: "Kanit", sans-serif;
  width: 100%;
  height: 4vh;
  color: rgb(170, 170, 170);
  background: rgb(247, 247, 247);
  border: 1px solid rgb(220, 220, 220);
  padding-left: 10px;
}

.modalCloseButtonContainer {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.modalCloseButton {
  text-align: center;
  width: 300px;
  height: 20px;
  color: rgb(78, 78, 78);
  background: rgb(245, 245, 245);
  border: 1px solid rgb(184, 183, 183);
  border-width: 0.1px;
  border-radius: 2px;
  font-family: "Kanit", sans-serif;
  font-size: 10px;
}

.rightBody {
  grid-area: rightBody;
  border: none;
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.ordinaryText {
  font-size: 1.5vmin;
  color: rgb(78, 78, 78);
}

input[type="radio"] {
  transform: scale(0.75);
}

.moreoverdiv {
  margin-top: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.moreoverbutton {
  text-align: center;
  width: 350px;
  height: 40px;
  color: rgb(78, 78, 78);
  background: white;
  border: 1px solid rgb(184, 183, 183);
  border-width: 0.1px;
  border-radius: 2px;
  font-family: "Kanit", sans-serif;
}

@media screen and (max-width: 480px) {
  .grid {
    width: auto;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding-left: 10px;
    padding-right: 10px;
  }

  .leftBody {
    display: none;
  }

  .provinceBody {
    display: none;
  }

  .provinceSelector {
    font-family: "Kanit", sans-serif;
    width: 100%;
    height: 4vh;
    border: 1px solid rgb(220, 220, 220);
    padding-left: 10px;
    background: rgb(247, 247, 247);
    color: rgb(170, 170, 170);
  }

  .rightBody {
    grid-area: rightBody;
    border: none;
    min-width: 480px;
    min-height: 1000px;
    padding: 10px;
  }
}

.merchantBox {
  width: auto;
  height: 30vh;
  background-color: rgb(255, 255, 255);
  border: 1px solid rgb(210, 230, 240);
  margin-bottom: 5px;
  padding: 4px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.titleContainer {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

.openCloseSticker {
  margin-top: 1vh;
}

.isOpenSticker {
  margin-left: 10px;
  width: 3vw;
  height: 2vh;
  border-radius: 2px;
  color: white;
  background-color: rgb(43, 190, 6);
  font-size: 9px;
  font-weight: 700;
  padding: 1px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.isCloseSticker {
  margin-left: 10px;
  width: 3vw;
  height: 2vh;
  border-radius: 2px;
  color: white;
  background-color: rgb(182, 179, 179);
  font-size: 9px;
  font-weight: 700;
  padding: 1px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.secondTitleContainer {
  font-size: 1.8vh;
  margin-top: 0.5vh;
  color: rgb(140, 140, 140);
}

.keedkeed {
  margin-top: 2vh;
  margin-bottom: 1vh;
  font-size: 0.8vh;
  color: rgb(200, 200, 200);
}
.merchantImg {
  float: left;
  width: 16.5vw;
  height: 100%;
  border-radius: 4px;
}

.imgClosedContainer {
  display: flex;
  justify-content: center;
  align-items: center;
}

.merchantImgClosed {
  float: left;
  width: 16.5vw;
  height: 100%;
  border-radius: 4px;
  opacity: 0.6;
}

.NAimg {
  position: absolute;
  width: 5vw;
  height: 10vh;
  z-index: 10;
}

.merchantTextbox {
  margin-left: 20px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.iconImg {
  margin-top: 0.3vw;
  margin-left: 0.3vw;
  width: 2vw;
  height: 4vh;
}

.shopNameText {
  margin-top: 5px;
  font-size: 2.5vmin;
  font-weight: 1000;
}

.additionalInfo {
  font-size: 1.8vh;
  margin-top: 2vh;
  color: rgb(140, 140, 140);
}

.boldmenu {
  color: rgb(94, 94, 94);
}

.facility {
  margin-top: 1.7vh;
}

@media screen and (max-width: 480px) {
  .merchantBox {
    width: auto;
    height: auto;
    border: 1px solid rgb(210, 230, 240);
    padding: 0px;
    margin-bottom: 5px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }

  .merchantImg {
    width: 100%;
    height: 40vh;
    border-radius: 4px;
  }

  .merchantImgClosed {
    min-width: 100%;
    height: 40vh;
    border-radius: 4px;
    opacity: 0.6;
  }

  .NAimg {
    position: absolute;
    width: 22vw;
    height: 13vh;
    z-index: 10;
  }

  .shopNameText {
    margin-top: 3vh;
    font-size: 5vmin;
    font-weight: 1000;
  }

  .openCloseSticker {
    margin-top: 3.5vh;
  }

  .isOpenSticker {
    margin-left: 2vw;
    width: 40px;
    height: 1.5vh;
    border-radius: 2px;
    color: white;
    background-color: rgb(43, 190, 6);
    font-size: auto;
    font-weight: 700;
    padding: 1px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .isCloseSticker {
    margin-left: 2vw;
    width: 40px;
    height: 1.5vh;
    border-radius: 2px;
    color: white;
    background-color: rgb(182, 179, 179);
    font-size: auto;
    font-weight: 700;
    padding: 1px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .iconImg {
    margin-top: 0.3vw;
    margin-left: 0.3vw;
    margin-bottom: 4vw;
    width: 8vw;
    height: 3.5vh;
  }

  .keedkeed {
    margin-top: 2vh;
    margin-bottom: 1vh;
    font-size: 0.4vh;
    color: rgb(200, 200, 200);
  }

  .secondTitleContainer {
    font-size: 1.2vh;
    margin-top: 0.5vh;
    color: rgb(140, 140, 140);
  }

  .additionalInfo {
    font-size: 1.2vh;
    margin-top: 2vh;
    color: rgb(140, 140, 140);
  }
}

